a {
    text-decoration: none;
    color: rgb(210, 180, 180);
  }
  a:hover {
    text-decoration: underline;
    color: #f4e6e6;
  }
  
  h1 {
    margin-top: 100px;
    margin-bottom: 40px;
  }
  
  h4 {
    margin-bottom: 40px;
  }
  
  .WelcomeScreen {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background: #141414;
  }
  
  .login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    border: 4px solid #494949;
    display: inline-block;
    transition-property: background-color, box-shadow;
    transition-duration: 150ms;
    transition-timing-function: ease-in-out;
    min-width: 10rem;
    margin: 1rem;
  }
  
  .google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: rgb(110, 159, 182);
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }
  
  .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
  }
  
  .google-btn:active {
    background: #1669f2;
  }
  
  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: inherit;
  }
  
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
  }
  
  .btn-text {
    display: inline-block;
    /* float: right; */
    color: white;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
    background-color: inherit;
    text-align: center;
    cursor: pointer;
    border: none;
  }
  
  .recharts-wrapper {
    z-index: -1;
  }